import { useQuery } from '@tanstack/react-query';
import {
  PostContent,
  PostFile,
  PostType,
  postTypeToTag,
  PublicClient,
} from '@youfoundation/dotyoucore-js';
import useAuth from '../auth/useAuth';

type useBlogPostsProps = {
  channelId?: string;
  postType?: PostType;
  pageSize?: number;
};

const useBlogPosts = ({ channelId, postType, pageSize = 10 }: useBlogPostsProps) => {
  const { getSharedSecret, getApiType } = useAuth();

  const fetchBlogData = async ({ channelId }: { channelId?: string }) => {
    const client = new PublicClient({ api: getApiType(), sharedSecret: getSharedSecret() });
    const fileData = await client.fileReadOnlyProvider.GetFile('blogs.json');

    let foundBlogPosts: PostFile<PostContent>[];
    // If located in a specific channel, only fetch those, otherwise fallback to recents;
    if (channelId) {
      if (fileData.has(channelId)) {
        foundBlogPosts =
          fileData
            .get(channelId)
            ?.map((entry) => {
              return {
                content: entry.payload,
                previewThumbnail: entry.header.fileMetadata.appData.previewThumbnail,
              } as PostFile<PostContent>;
            })
            .filter((post) => (postType ? post.content.type === postType : true)) ?? [];
      } else {
        foundBlogPosts = (
          await client.blogPostReadonlyProvider.getPosts(
            channelId,
            postType ? [postTypeToTag(postType).toString()] : undefined,
            undefined,
            pageSize
          )
        ).results;
      }
    } else {
      if (fileData.size >= 1) {
        foundBlogPosts = [];

        for (const channelId of fileData.keys()) {
          foundBlogPosts = [
            ...foundBlogPosts,
            ...(fileData
              .get(channelId)
              ?.map((entry) => {
                return {
                  content: entry.payload,
                  previewThumbnail: entry.header.fileMetadata.appData.previewThumbnail,
                } as PostFile<PostContent>;
              })
              ?.filter((post) => (postType ? post.content.type === postType : true)) ?? []),
          ];

          // Sorted descending
          foundBlogPosts.sort((a, b) => b?.content?.dateUnixTime - a?.content?.dateUnixTime);
        }
      } else {
        foundBlogPosts = (
          await client.blogPostReadonlyProvider.getRecentPosts(
            postType ? [postTypeToTag(postType).toString()] : undefined,
            pageSize
          )
        ).results;
      }
    }

    return foundBlogPosts
      .filter((post) => post !== undefined && post?.content?.id !== undefined && !post.isDraft)
      .slice(0, pageSize);
  };

  return useQuery(['blog-recents', channelId], () => fetchBlogData({ channelId }), {
    refetchOnMount: false,
    refetchOnWindowFocus: false,
    onError: (er) => {
      console.log(er);
    },
  });
};

export default useBlogPosts;
