import { ChannelDefinition, PostContent, PostFile } from '@youfoundation/dotyoucore-js';
import { t } from '../../../../helpers/i18n/dictionary';
import useBlogPosts from '../../../../hooks/blog/useBlogPosts';
import { ChannelDefinitionVm } from '../../../../hooks/blog/useChannels';
import PostTeaser from '../Card/PostTeaser';

const relatedBlogs = ({
  blog,
  channel,
}: {
  blog: PostFile<PostContent>;
  channel: ChannelDefinitionVm | ChannelDefinition | undefined;
}) => {
  const { data: blogPosts } = useBlogPosts(
    channel ? { channelId: channel.channelId, postType: 'Article' } : {}
  );

  if (!blogPosts) {
    return <></>;
  }

  const filteredBlogPosts = blogPosts
    .slice(0, 4)
    .filter((relatedBlog) => relatedBlog.content.id !== blog.content.id)
    .slice(0, 3);

  return (
    <section className="mt-10 bg-slate-50 pt-16 pb-10 dark:bg-slate-800 dark:bg-opacity-50">
      <div className="container mx-auto px-5">
        <h2 className="mb-10 text-2xl ">
          {t('Latest posts')} {channel && <small>| {channel.name}</small>}
        </h2>
        <div className="-m-1 flex flex-row flex-wrap">
          {filteredBlogPosts.map((postFile) => {
            return (
              <PostTeaser
                postFile={postFile}
                key={postFile.content.id}
                className={`p-1 sm:w-1/2 md:w-1/3`}
              />
            );
          })}
        </div>
      </div>
    </section>
  );
};

export default relatedBlogs;
