import { TargetDrive } from '@youfoundation/dotyoucore-js';
import React, { ReactNode } from 'react';
import Image from '../Image/Image';
import ActionLink from '../ui/Buttons/ActionLink';

const RichTextRenderer = ({
  body,
  imageDrive,
}: {
  body: string | Record<string, unknown>[] | undefined;
  imageDrive?: TargetDrive;
}) => {
  if (!body || typeof body === 'string') {
    return <>{body}</>;
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const render = (node: any): ReactNode => {
    if ('text' in node) {
      return renderLeaf(node, node.text, {});
    } else {
      const { type, ...attributes } = node;

      return renderElement(
        { type, attributes },
        <>
          {
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            node.children?.map((childNode: any, index: number) => (
              <React.Fragment key={index}>{render(childNode)}</React.Fragment>
            ))
          }
        </>
      );
    }
  };

  const renderLeaf = (
    leaf: { text?: string; bold?: boolean; italic?: boolean; underline?: boolean; code?: boolean },
    children: ReactNode,
    attributes: Record<string, unknown>
  ) => {
    if (leaf.bold) {
      children = <strong className="font-bold">{children}</strong>;
    }

    if (leaf.code) {
      children = <code>{children}</code>;
    }

    if (leaf.italic) {
      children = <em>{children}</em>;
    }

    if (leaf.underline) {
      children = <u>{children}</u>;
    }

    return <span {...attributes}>{children}</span>;
  };

  const renderElement = (
    node: {
      type?: string;
      attributes?: Record<string, unknown>;
    },
    children: ReactNode
  ) => {
    const { type, attributes } = node;

    switch (type) {
      case 'block-quote':
        return (
          <blockquote {...attributes} className="border-l-4 pl-2">
            {children}
          </blockquote>
        );
      case 'bulleted-list':
        return (
          <ul {...attributes} className="list-disc pl-5">
            {children}
          </ul>
        );
      case 'heading-one':
        return (
          <h1 {...attributes} className={'text-2xl'}>
            {children}
          </h1>
        );
      case 'heading-two':
        return (
          <h2 {...attributes} className={'text-xl'}>
            {children}
          </h2>
        );
      case 'list-item':
        return <li {...attributes}>{children}</li>;
      case 'numbered-list':
        return (
          <ol {...attributes} className="list-decimal pl-5">
            {children}
          </ol>
        );
      case 'image':
        if (attributes && 'imageFileId' in attributes && imageDrive) {
          return (
            <Image
              targetDrive={imageDrive}
              fileId={attributes.imageFileId as string}
              className="my-4 max-w-md"
            />
          );
        }
        return <></>;
      case 'link-button':
        if (attributes && 'linkText' in attributes && attributes && 'uri' in attributes) {
          return (
            <div className="flex ">
              <ActionLink
                className="my-2 w-auto rounded-md bg-gray-500 px-3 py-2 text-left"
                href={attributes.uri as string}
                type="secondary"
              >
                {attributes.linkText as string}
              </ActionLink>
            </div>
          );
        }
        return <></>;
      default:
        return (
          <p {...attributes} className="mb-3">
            {children}
          </p>
        );
    }
  };

  return (
    <div>
      {body.map((element, index) => {
        return <React.Fragment key={index}>{render(element)}</React.Fragment>;
      })}
    </div>
  );
};

export default RichTextRenderer;
