import { Article, getChannelDrive, Image as ImageType } from '@youfoundation/dotyoucore-js';
import { Link, useParams } from 'react-router-dom';
import useBlog from '../../../hooks/blog/useBlog';
import Image from '../../../components/Image/Image';
import RichTextRenderer from '../../../components/RichTextRenderer/RichTextRenderer';
import RelatedBlogs from '../../../components/Blog/Common/RelatedBlogs/RelatedBlogs';
import { Helmet } from 'react-helmet-async';
import { t } from '../../../helpers/i18n/dictionary';
import Triangle from '../../../components/ui/Icons/Triangle/Triangle';
import LoadingParagraph from '../../../components/ui/LoadingParagraph/LoadingParagraph';
import PostMeta from '../../../components/Blog/Common/Blocks/Meta/Meta';
import ImageGallery from '../../../components/Blog/Common/Blocks/ImageGallery/ImageGallery';

const BlogDetail = () => {
  const { channelKey, postKey } = useParams();
  const { data: postData, isLoading: postDataLoading } = useBlog(
    channelKey && postKey
      ? {
          channelSlug: channelKey,
          blogSlug: postKey,
        }
      : undefined
  );

  if (!postData && !postDataLoading) {
    return (
      <section className="py-5">
        <div className="container mx-auto mb-10 px-5">{t('No post found')}</div>
      </section>
    );
  }

  const post = postData?.activeBlog.content;
  const channel = postData?.activeChannel;

  const imageFileIds = (post as ImageType)?.imageFileIds;

  return (
    <>
      <Helmet>
        <title>{post?.caption ?? ''} | Youniverse</title>
      </Helmet>

      <section className="py-5">
        <div className="container mx-auto mb-10 px-5">
          <div className="max-w-3xl lg:w-2/3">
            <div className="mb-5 flex w-full flex-col">
              <div className="flex flex-row flex-wrap pb-2 text-gray-500">
                {!post ? (
                  <LoadingParagraph className="mb-2 h-8 w-full max-w-xs" />
                ) : (
                  <>
                    {post.type === 'Article' && (post as Article)?.readingTimeStats && (
                      <>
                        <p className="title-font text-sm font-medium">
                          {Math.ceil((post as Article)?.readingTimeStats?.minutes ?? 0)}
                          {t('min read')}
                        </p>
                        <span className="px-2 leading-4">Â·</span>
                      </>
                    )}
                    {post && channel ? (
                      <PostMeta post={post} channel={channel} size="text-sm" />
                    ) : null}
                  </>
                )}
              </div>
              {!post ? (
                <LoadingParagraph className="h-8 w-full max-w-xs" />
              ) : (
                <h1 className="title-font mb-4 text-2xl font-medium sm:text-3xl">{post.caption}</h1>
              )}
            </div>

            {post?.primaryImageFileId ? (
              imageFileIds && imageFileIds.length > 1 ? (
                <ImageGallery
                  channelId={post.channelId}
                  fileIds={imageFileIds}
                  className="my-4"
                  maxVisible={4}
                  postUrl={window.location.pathname}
                />
              ) : (
                <div className="relative mb-5 sm:w-full">
                  <Link to="0">
                    <Image
                      className="rounded object-cover object-center"
                      fileId={post.primaryImageFileId}
                      targetDrive={getChannelDrive(post.channelId)}
                      alt="blog"
                      previewThumbnail={postData?.activeBlog.previewThumbnail}
                    />
                  </Link>
                  {post.type === 'Video' && (
                    <Triangle className="absolute top-[calc(50%-2.5rem)] left-[calc(50%-2.5rem)] h-20 w-20 text-white" />
                  )}
                </div>
              )
            ) : null}

            {post?.type === 'Article' ? (
              <p className="mb-5 text-base leading-relaxed text-gray-500">
                {(post as Article)?.abstract}
              </p>
            ) : null}

            {!post ? (
              <>
                <LoadingParagraph className="mb-2 h-4 w-full" />
                <LoadingParagraph className="mb-2 h-4 w-full" />
                <LoadingParagraph className="mb-2 h-4 w-full" />
                <LoadingParagraph className="mb-2 h-4 w-full" />
                <LoadingParagraph className="mb-2 h-4 w-full" />
              </>
            ) : (
              <>
                {post.type === 'Article' && (
                  <div className="rich-text-content mb-5 leading-relaxed">
                    <RichTextRenderer
                      body={(post as Article)?.body}
                      imageDrive={getChannelDrive(post.channelId)}
                    />
                  </div>
                )}
              </>
            )}
          </div>
        </div>
      </section>
      {postData?.activeBlog?.content?.type === 'Article' && (
        <RelatedBlogs blog={postData?.activeBlog} channel={channel} />
      )}
    </>
  );
};

export default BlogDetail;
