import { Article, PostContent, getChannelDrive, PostFile } from '@youfoundation/dotyoucore-js';
import { FC } from 'react';
import { Link } from 'react-router-dom';
import Image from '../../../Image/Image';
import Arrow from '../../../ui/Icons/Arrow/Arrow';
import { t } from '../../../../helpers/i18n/dictionary';
import Triangle from '../../../ui/Icons/Triangle/Triangle';
import Bubble from '../../../ui/Icons/Bubble/Bubble';
import useChannel from '../../../../hooks/blog/useChannel';
import { ellipsisAtMaxChar } from '../../../../helpers/common';

interface PostTeaserProps {
  className?: string;
  postFile: PostFile<PostContent>;
  isSingleCol?: boolean;
  hideImageWhenNone?: boolean;
  showChannel?: boolean;
}

const PostTeaser: FC<PostTeaserProps> = ({
  className,
  postFile: { content: post, previewThumbnail },

  isSingleCol,
  hideImageWhenNone,
  showChannel,
}) => {
  const { data: channel } = useChannel({ channelId: post.channelId });

  return (
    <div className={`w-full ${className ?? ''}`}>
      <Link to={`/home/posts/${channel?.slug}/${post.slug ?? '#'}`} className="contents">
        <div className="relative h-full overflow-hidden rounded-lg border border-gray-200 border-opacity-60 transition-colors hover:shadow-md dark:border-gray-800 hover:dark:shadow-slate-600 ">
          {(!hideImageWhenNone || post.primaryImageFileId) && (
            <div className={`relative ${isSingleCol ? 'sm:p-6 sm:pb-2' : ''}`}>
              {post.primaryImageFileId ? (
                <>
                  <Image
                    className={`${isSingleCol && 'sm:rounded-lg'} ${
                      post.type === 'Image' || post.type === 'Video'
                        ? 'h-full max-h-[30rem] w-auto'
                        : 'h-64 w-full md:h-36 lg:h-48'
                    } ${post.type === 'Image' && ''} ${post.type === 'Video' && 'aspect-video'}`}
                    fileId={post.primaryImageFileId}
                    targetDrive={getChannelDrive(post.channelId)}
                    alt="blog"
                    fit="cover"
                    previewThumbnail={previewThumbnail}
                  />
                  {post.type === 'Video' && (
                    <>
                      <Triangle className="absolute top-[calc(50%-2.5rem)] left-[calc(50%-2.5rem)] h-20 w-20 text-white" />
                    </>
                  )}
                </>
              ) : !hideImageWhenNone ? (
                <div
                  className={`max-w-full overflow-hidden text-ellipsis whitespace-nowrap bg-slate-50 text-slate-200 dark:bg-slate-700 dark:text-slate-600 ${
                    isSingleCol && 'sm:rounded-lg'
                  } ${
                    post.type === 'Image' || post.type === 'Video'
                      ? 'h-full max-h-[30rem] w-auto'
                      : 'h-64 w-full md:h-36 lg:h-48'
                  } ${post.type === 'Video' && 'aspect-video'}`}
                >
                  <p className="p-2 text-9xl ">{post.caption}</p>
                </div>
              ) : null}
            </div>
          )}
          <div className="px-6 py-4">
            <div className="flex flex-row text-foreground text-opacity-40">
              <span className="my-auto text-xs">
                {new Date(post.dateUnixTime).toLocaleDateString(undefined, {
                  month: 'short',
                  day: 'numeric',
                  year: 'numeric',
                })}
              </span>
              <h2 className="my-auto ml-2 border-l pl-2 text-xs font-medium uppercase tracking-wide dark:border-slate-500">
                {post.type}
                {showChannel && channel?.name ? ` - ${channel?.name}` : ''}
              </h2>
            </div>

            <h1 className="text-lg font-medium text-foreground text-opacity-90">{post.caption}</h1>
            {post.type === 'Article' ? (
              <>
                <p className="my-3 leading-relaxed">
                  {ellipsisAtMaxChar((post as Article).abstract, 240)}
                  {(post as Article)?.abstract?.length > 240 ? (
                    <span className="text-button">{t('Read more')}</span>
                  ) : null}
                </p>
                <div className="flex flex-wrap items-center ">
                  <span className="inline-flex items-center text-button md:mb-2 lg:mb-0">
                    {t('Read more')}
                    <Arrow className="ml-2 h-4 w-4" />
                  </span>
                </div>
              </>
            ) : null}
          </div>
          <div className="absolute bottom-0 right-0 flex items-center p-5 text-sm text-gray-300">
            <span className="inline-flex items-center">
              <Bubble className="mr-1 inline-block h-4 w-4" /> 6
            </span>
          </div>
        </div>
      </Link>
    </div>
  );
};

export default PostTeaser;
